var that;
import areaData from './area-data-min.js'
import ImageCropper from "@/components/invinbg-image-cropper/invinbg-image-cropper.vue";
export default {
	components: {
		ImageCropper
	},
	data() {
		return {
			name:'新用户',
			birthday: '2000-05-01',
			tupian: 'https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/images/2020/03/20/fe6861f82870aa9826f7e01ea8878235.png',
			tempFilePath: '',
			cropFilePath: '',
			themeColor: '#65c186',
			list: [{
					label: '男',
					value: 1
				},
				{
					label: '女',
					value: 2
				},
				{
					label: '保密',
					value: 0
				}
			],
			sex: '男',
			value5: ['610000', '610100', '610113'],
			list5: areaData,
			address: '陕西省西安市雁塔区',
			info: ''
		}
	},
	computed: {

		endDate() {
			return this.getDate('end');
		}
	},
	onLoad: function(e) {
		that = this;
		uni.request({
			url: this.$api_url + '/user/get_user_info', //仅为示例，并非真实接口地址。
			method: 'GET',
			header: {
				'authorization': uni.getStorageSync('authorization') //自定义请求头信息
			},
			success: res => {
				console.log(res)
				if (res.data.code == 1000) {
					that.info = res.data.content.intro;
					that.address = res.data.content.address;
					that.sex = res.data.content.sexName;
					that.birthday = res.data.content.birthday;
					that.name = res.data.content.userName;
					that.tupian = res.data.content.headimg;
				}
			}
		});
	},
	onShow: function(res) {

	},
	methods: {
		upload() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ["camera", 'album'], //从相册选择
				success: (res) => {
					that.tempFilePath = res.tempFilePaths.shift();
				}
			});
		},
		confirm(e) {
			that.tempFilePath = ''
			that.cropFilePath = e.detail.tempFilePath
			uni.uploadFile({
				url: that.$api_url + '/file/image_upload',
				filePath: that.cropFilePath,
				name: 'upload_file',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				success: (res) => {
					var json = JSON.parse(res.data)
					if (json.code == 1000) {
						console.log(json);
						that.tupian = json.content.url

						uni.request({
							url: this.$api_url + '/user/update_headimg', //仅为示例，并非真实接口地址。
							method: 'POST',
							header: {
								'Content-Type': 'application/x-www-form-urlencoded',
								'authorization': uni.getStorageSync('authorization') //自定义请求头信息
							},
							data: {
								headimg: that.tupian
							},
							success: res => {
								console.log(res)
								if (res.data.code == 1000) {
									console.log("ok")
								}
							}
						});


					}
				},
				fail: (res) => {
					console.log(res)
				}
			});

		},
		cancel() {
			console.log('canceled')
		},
		handleTap() {
			this.$refs.picker.show()
		},
		handleChange(item) {
			console.log('change::', item)
		},
		handleConfirm(item) {
			this.sex = item.item.label
			console.log('confirm::', item)
		},
		handleCancle(item) {
			console.log('cancle::', item)
		},

		handleTap5() {
			this.$refs.picker5.show()
		},
		handleChange5(item) {
			console.log('change::', item)
		},
		handleConfirm5(item) {
			console.log('confirm::', item)
			this.address = item.item[0].label + item.item[1].label + item.item[2].label
		},
		handleCancle5(item) {
			console.log('cancle::', item)
		},
		bindDateChange: function(e) {
			this.birthday = e.target.value
			console.log(e)
		},

		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		updatainfo: function() {
			if (that.name == '' || that.name == null) {
				uni.showToast({
					title: '请输入昵称'
				})
				return
			}
			if (that.info == '' || that.info == null) {
				uni.showToast({
					title: '请输入简介'
				})
				return
			}
			uni.request({
				url: this.$api_url + '/user/update_user_info', //仅为示例，并非真实接口地址。
				method: 'POST',
				header: {
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				data: {
					    "birthday": that.birthday,
					    "userName": that.name,
					    "address": that.address,
					    "intro": that.info,
					    "sexName": that.sex,
				},
				success: res => {
					console.log(res)
					if (res.data.code == 1000) {
						console.log(res)
						uni.showToast({
							title:''+res.data.message
						})
					}
				}
			});
		}
	}

}
